.wordsListWrapper{
	margin-right: auto;
	margin-left: auto;
	width: 100%;
	background-color: #fff;
}

@media only screen and (min-width: 600px) {
	.wordsListWrapper{
		margin-top: 15px;
		margin-right: auto;
		margin-left: auto;
		height: auto;
		width: 80%;
		background-color: #fff;
	}
}

@media only screen and (min-width: 900px) {
	.wordsListWrapper{
		margin-top: 15px;
		margin-right: auto;
		margin-left: auto;
		height: auto;
		width: 70%;
		background-color: #fff;
	}
}

.wordsListNewWordWrapper{
	margin: 5px 0px;
    background-color: #3CAB9E;
    width: 100%;
    min-height: 35px;
    overflow-y: auto;
}

.wordsListOverflow{
	overflow-y: auto;
}

.newWordSpelling{
	font-weight: bold;
	display: inline-block;
}

.newWordExplanation{
	display: inline-block;
}

.newWordSpellingExplanationWrapper{
	margin: 10px 10px 0px 10px;
}

.newWordExamples{
	margin: 5px;
	padding: 5px;
	background-color: #fff;
}

.newWordValue{
	display: inline-block;
	float: right;
}


