
.dashboardBookWrapper{
	float: left;
	cursor: pointer;
	width: 150px;
	height: 250px;
	background-color: #fff;
	margin: 10px 5px;
}

.dashboardBook{
	width: 150px;
	height: 250px;
	-webkit-box-shadow: 0 4px 2px -2px gray;
	        box-shadow: 0 4px 2px -2px gray;
}

.dashboardBookWrapper:hover{
	float: left;
	cursor: pointer;
	width: 150px;
	height: 250px;
	background-color: #fff;
	margin: 10px 5px;
	opacity: 0.7;
}

.dashboardBook:hover{
	width: 150px;
	height: 250px;
	-webkit-box-shadow: 0 4px 2px -2px gray;
	        box-shadow: 0 4px 2px -2px gray;
	opacity: 0.7;
}

